import React,{ Component } from 'react';
import { Collapse, Navbar, NavbarToggler, Nav, NavItem } from 'reactstrap';
import { Link, animateScroll as scroll } from "react-scroll";
import About from './components/About/About';
import Education from './components/Education/Education';
import Skills from './components/Skills/Skills';
import Projects from './components/Projects/Projects';
import Highlights from './components/Highlights/Highlights';
import Interests from './components/Interests/Interests';
import './App.css';


class App extends Component {
  state = {
    collapsed: null,
    isGr: null
  }

  constructor(props) {
    super(props);
    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.switchLang = this.switchLang.bind(this);
    this.state = {
      collapsed: true,
      isGr: false
    };
  }

  scrollToTop = () => {
    scroll.scrollToTop();
  };

  toggleNavbar() {
    this.setState({
      collapsed: !this.state.collapsed
    });
  }

  switchLang() {
    if(this.state.isGr){
      let newState = {...this.state.isGr, isGr: false};
      this.setState(newState);
    }
    else{
      let newState = {...this.state.isGr, isGr: true};
      this.setState(newState);
    }
  }

  render(){
    var lang_icon;
    if(!this.state.isGr){
      lang_icon = <img className="flag" src={require('./assets/images/GR.png')} alt="eng"/>;
    }
    else{
      lang_icon = <img className="flag" src={require('./assets/images/GB.png')} alt="gr"/>;
    }

    return (
    <div className="App">
        {
         /* Language Switcher */
        }
        <div className="switcher" onClick={this.switchLang}>
          {lang_icon}
        </div>
        {
          /* Navbar */
        }
        <Navbar className="navbar navbar-expand-lg navbar-dark bg-primary fixed-top" id="sideNav">
          <a className="navbar-brand" href="#about">
            <span className="d-block d-lg-none">
            {this.state.isGr ? 'Ευάγγελος Γκαραγκάνης' : 'Vangelis Garaganis'}
            </span>
            <span className="d-none d-lg-block">
              <img className="img-fluid img-profile rounded-circle mx-auto mb-2" src={require('./assets/images/profile.jpg')} alt="" onClick={this.scrollToTop} />
            </span>
          </a>
          <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />
          <Collapse isOpen={!this.state.collapsed} navbar>
            <Nav navbar>
              <NavItem>
                <Link activeClass="active" to="about" spy={true} smooth={true} offset={-70} duration={500}>
                <a className="nav-link" href="#about">
                  {this.state.isGr ? 'Ποιος Ειμαι' : 'About'}
                </a>
                </Link>
              </NavItem>
              <NavItem>
                <Link activeClass="active" to="education" spy={true} smooth={true} offset={0} duration={500}>
                <a className="nav-link" href="#education">
                  {this.state.isGr ? 'Εκπαιδευση' : 'Education'}
                </a>
                </Link>
              </NavItem>
              <NavItem>
                <Link activeClass="active" to="skills" spy={true} smooth={true} offset={0} duration={500}>
                <a className="nav-link" href="#skills">
                  {this.state.isGr ? 'Ικανοτητες' : 'Skills'}
                </a>
                </Link>
              </NavItem>
                <NavItem>
                <Link activeClass="active" to="projects" spy={true} smooth={true} offset={0} duration={500}>
                <a className="nav-link" href="#projects">
                  {this.state.isGr ? 'Δουλειες Μου' : 'Projects'}
                </a>
                </Link>
              </NavItem>
              <NavItem>
                <Link activeClass="active" to="highlights" spy={true} smooth={true} offset={0} duration={500}>
                <a className="nav-link" href="#highlights">
                  {this.state.isGr ? 'Αξιοσημειωτα' : 'Highlights'}
                </a>
                </Link>
              </NavItem>
              <NavItem>
                <Link activeClass="active" to="interests" spy={true} smooth={true} offset={0} duration={500}>
                <a className="nav-link" href="#interests">
                  {this.state.isGr ? 'Ενδιαφεροντα' : 'Interests'}
                </a>
                </Link>
              </NavItem>
              <NavItem>
                  <p style={{cursor:'pointer'}} className="nav-link-switcher" onClick={this.switchLang}>
                  {this.state.isGr ? 'switch to english' : 'switch to greek'}
                </p>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
        {
          /* Container */
        }
        <div className="container-fluid p-0">
          {
            /* About Section */
          }
          <section className="resume-section p-3 p-lg-5 d-flex d-column" id="about">
            <About isgreek = {this.state.isGr}/>
          </section>
          {
            /* Education Section */
          }
          <hr className="m-0"/>
          <section className="resume-section p-3 p-lg-5 d-flex flex-column" id="education">
            <Education isgreek = {this.state.isGr}/>
          </section>
          {
            /* Skills Section */
          }
          <hr className="m-0"/>
          <section className="resume-section p-3 p-lg-5 d-flex flex-column" id="skills">
            <Skills isgreek = {this.state.isGr}/>
          </section>
          {
            /* Projects Section */
          }
          <hr className="m-0"/>
          <section className="resume-section p-3 p-lg-5 d-flex flex-column" id="projects">
            <Projects isgreek={this.state.isGr}/>
          </section>
          {
            /* Highlights Section */
          }
          <hr className="m-0"/>
          <section className="resume-section p-3 p-lg-5 d-flex flex-column" id="highlights">
            <Highlights isgreek={this.state.isGr}/>
          </section>
          {
            /* Intersets Section */
          }
          <hr className="m-0"/>
          <section className="resume-section p-3 p-lg-5 d-flex flex-column" id="interests">
            <Interests isgreek = {this.state.isGr}/>
          </section>
        </div>
    </div>
  );
  }
}

export default App;
