import React, { Component } from 'react';
import './Projects.css';
import AI_Projects from './Projects_json/AI.json';
import WD_Projects from './Projects_json/WD.json';
import DS_Projects from './Projects_json/DS.json';
import LP_Projects from './Projects_json/LP.json';
import COMPS_Projects from './Projects_json/COMPS.json';

class projects extends Component {
  state = {
    ai_projects: [],
    ai_counter: null,
    wd_projects: [],
    wd_counter: null,
    ds_projects: [],
    ds_counter: null,
    lp_projects: [],
    lp_counter: null,
    comps_projects: [],
    compos_counter: null
  }

  constructor(props) {
    super(props);
    const first3_ai_projects = AI_Projects.slice(0, 3);
    const first3_wd_projects = WD_Projects.slice(0, 3);
    const first3_ds_projects = DS_Projects.slice(0, 3);
    const first3_lp_projects = LP_Projects.slice(0, 3);
    const first3_comps_projects = COMPS_Projects.slice(0, 3);
    this.state = {ai_projects: first3_ai_projects,ai_counter: 0,
                  wd_projects: first3_wd_projects,wd_counter: 0,
                  ds_projects: first3_ds_projects,ds_counter: 0,
                  lp_projects: first3_lp_projects,lp_counter: 0,
                  comps_projects: first3_comps_projects,comps_counter: 0,
                };
  }

  getProjectCards = (projects_category) => {
    var card_uniq_key = 0;
    var par_uniq_key = 0;
    var projects_arr;
    var proj_counter;
    if( projects_category === "ai"){
      projects_arr = this.state.ai_projects;
      proj_counter = this.state.ai_counter;
    }
    else if( projects_category === "wd"){
      projects_arr = this.state.wd_projects;
      proj_counter = this.state.wd_counter;
    }
    else if( projects_category === "ds"){
      projects_arr = this.state.ds_projects;
      proj_counter = this.state.ds_counter;
    }
    else if( projects_category === "lp"){
      projects_arr = this.state.lp_projects;
      proj_counter = this.state.lp_counter;
    }
    else{
      projects_arr = this.state.comps_projects;
      proj_counter = this.state.comps_counter;
    }
    var projects_cards = Object.keys(projects_arr).map( key => {
      var langs = projects_arr[key].langs.map( lang => {
        par_uniq_key++;
        return <p className="lang" key={"p" + projects_category + proj_counter + par_uniq_key}> {lang} </p>
      });
      var git_site;
      if (projects_arr[key].git === "bucket") {
        // eslint-disable-next-line
        git_site =  <a href={projects_arr[key].code} target="_blank"><i className="fab fa-bitbucket"></i></a>;
      }
      else
      {
        // eslint-disable-next-line
        git_site =  <a href={projects_arr[key].code} target="_blank"><i className="fab fa-github"></i></a>;
      }
      card_uniq_key++;
      return (
      <div className="card mycard" key={"c" + proj_counter + card_uniq_key}>
        <div className="card-body">
          <h5 className="card-title"> {projects_arr[key].title} </h5>
          <p className="card-text"> {projects_arr[key].description} </p>
        </div>
        <div className="card-footer text-muted">
          <div className="row">
            <div className="col-9">
              <div className="lang"> {langs} </div>
            </div>
            <div className="col">
             {git_site}
            </div>
          </div>
        </div>
      </div>
    )});
    return projects_cards;
  }

  getNextProjects = (projects_category) => (event) => {
    if ( projects_category === "ai" ){
      const next_range = this.state.ai_counter + 3;
      const next3_ai_projects = AI_Projects.slice(next_range,next_range+3);
      this.setState({ai_projects:next3_ai_projects,ai_counter:next_range});
    }
    else if( projects_category === "wd"){
      const next_range = this.state.wd_counter + 3;
      const next3_wd_projects = WD_Projects.slice(next_range,next_range+3);
      this.setState({wd_projects:next3_wd_projects,wd_counter:next_range});
    }
    else if ( projects_category === "ds" ){
      const next_range = this.state.ds_counter + 3;
      const next3_ds_projects = DS_Projects.slice(next_range,next_range+3);
      this.setState({ds_projects:next3_ds_projects,ds_counter:next_range});
    }
    else if( projects_category === "lp"){
      const next_range = this.state.lp_counter + 3;
      const next3_lp_projects = LP_Projects.slice(next_range,next_range+3);
      this.setState({lp_projects:next3_lp_projects,lp_counter:next_range});
    }
    else {
      const next_range = this.state.comps_counter + 3;
      const next3_comps_projects = COMPS_Projects.slice(next_range,next_range+3);
      this.setState({comps_projects:next3_comps_projects,comps_counter:next_range});
    }
  }

  getPrevProjects = (projects_category) => (event) => {
    if ( projects_category === "ai" ){
      const prev_range = this.state.ai_counter - 3;
      const prev3_ai_projects = AI_Projects.slice(prev_range,prev_range+3);
      this.setState({ai_projects:prev3_ai_projects,ai_counter:prev_range});
    }
    else if( projects_category === "wd"){
      const prev_range = this.state.wd_counter - 3;
      const prev3_wd_projects = WD_Projects.slice(prev_range,prev_range+3);
      this.setState({wd_projects:prev3_wd_projects,wd_counter:prev_range});
    }
    else if ( projects_category === "ds" ){
      const prev_range = this.state.ds_counter - 3;
      const prev3_ds_projects = DS_Projects.slice(prev_range,prev_range+3);
      this.setState({ds_projects:prev3_ds_projects,ds_counter:prev_range});
    }
    else if( projects_category === "lp"){
      const prev_range = this.state.lp_counter - 3;
      const prev3_lp_projects = LP_Projects.slice(prev_range,prev_range+3);
      this.setState({lp_projects:prev3_lp_projects,lp_counter:prev_range});
    }
    else {
      const prev_range = this.state.comps_counter - 3;
      const prev3_comps_projects = COMPS_Projects.slice(prev_range,prev_range+3);
      this.setState({comps_projects:prev3_comps_projects,comps_counter:prev_range});
    }
  }

  getControls = (projects_category) => {
    var no_proj;
    var proj_counter;
    if( projects_category === "ai"){
      no_proj = AI_Projects.length;
      proj_counter = this.state.ai_counter;
    }
    else if( projects_category === "wd"){
      no_proj = WD_Projects.length;
      proj_counter = this.state.wd_counter;
    }
    else if( projects_category === "ds"){
      no_proj = DS_Projects.length;
      proj_counter = this.state.ds_counter;
    }
    else if( projects_category === "lp"){
      no_proj = LP_Projects.length;
      proj_counter = this.state.lp_counter;
    }
    else{
      no_proj = COMPS_Projects.length;
      proj_counter = this.state.comps_counter;
    }
    var next_projects = <span className="btn fas fa-angle-right next" onClick={this.getNextProjects(projects_category)}></span>;
    var prev_projects = <span className="btn fas fa-angle-right prev" onClick={this.getPrevProjects(projects_category)}></span>;
    if(proj_counter === 0){ prev_projects = null; }
    if( no_proj <= proj_counter + 3){ next_projects = null; }
    return(
      <div className="control">
          {next_projects}
          {prev_projects}
      </div>);
  }

  render(){
    var ai_projects_cards = this.getProjectCards("ai");
    var ai_projects_controls = this.getControls("ai");
    var wd_projects_cards = this.getProjectCards("wd");
    var wd_projects_controls = this.getControls("wd");
    var ds_projects_cards = this.getProjectCards("ds");
    var ds_projects_controls = this.getControls("ds");
    var lp_projects_cards = this.getProjectCards("lp");
    var lp_projects_controls = this.getControls("lp");
    var comps_projects_cards = this.getProjectCards("comps");
    var comps_projects_controls = this.getControls("comps");

    return(
      <div className="my-auto">
        <h2 className={"mb-4 " + (this.props.isgreek ? 'greekcathdr' : null)}>
          {this.props.isgreek ? 'Δουλειες Μου' : 'Projects'}
        </h2>
        {// AI PROJECTS
        }
        <h3 className="mb-0">Artificial Intelligence</h3>
        <div className="card-deck mycardeck">
          {ai_projects_cards}
          {ai_projects_controls}
        </div>
        {// Web Dev PROJECTS
        }
        <h3 className="mb-0" style={{marginTop: 2 + 'em'}}>Web Development</h3>
        <div className="card-deck mycardeck">
          {wd_projects_cards}
          {wd_projects_controls}
        </div>
        {// Data Structures PROJECTS
        }
        <h3 className="mb-0" style={{marginTop: 2 + 'em'}}>Data Structures</h3>
        <div className="card-deck mycardeck">
          {ds_projects_cards}
          {ds_projects_controls}
        </div>
        {// Low level PROJECTS
        }
        <h3 className="mb-0" style={{marginTop: 2 + 'em'}}>Low Level Programming</h3>
        <div className="card-deck mycardeck">
          {lp_projects_cards}
          {lp_projects_controls}
        </div>
        {// Compilers PROJECTS
        }
        <h3 className="mb-0" style={{marginTop: 2 + 'em'}}>Compilers</h3>
        <div className="card-deck mycardeck">
          {comps_projects_cards}
          {comps_projects_controls}
        </div>
      </div>
    );
  }
}

export default projects;
